<template>
  <div class="intention-preview-containter">
    <!-- 订单基本信息 -->
    <section class="base-info">
      <div class="base-info-header">
        <p class="header-number float-l">
          {{$t('order-meeting.intention.order_confirmation_number')}}：{{ info.orderId }}
        </p>
        <p class="header-saler float-r">
          {{$t('order-meeting.intention.sales_manager')}}：{{ info.salesManagerName }}
        </p>
        <p>
        <!--eslint-disable-next-line max-len-->
        {{$t('order-meeting.intention.order_submitssion_time')}}：{{ info.createTime | date('YYYY.MM.DD HH:mm:ss') }}
        </p>
      </div>

      <ul class="flex-wrap">
        <li v-for="item in clientInfoList" :key="item.title" class="base-info-item">
          <label>{{ item.title }}</label>
          <p>{{ item.name }}</p>
        </li>
      </ul>
    </section>

    <!-- 鞋款系列详情 -->
    <section class="shoe-series">
      <!-- 表单表头 -->
      <ul class="series-table-header">
        <li class="table-field-1"></li>
        <li class="table-field-2">{{$t('order-meeting.intention.product_info')}}</li>
        <li class="table-field-3">{{$t('order-meeting.intention.product_quantity')}}</li>
      </ul>

      <!-- 表单内容 -->
      <ul>
        <li v-for="(series, i) in info.shoeList" :key="i">
          <h3
            v-if="series.shoeDetails.length"
            class="series-title">{{ series.collectionName }}</h3>
          <ul>
            <li class="shoe-series-item" v-for="shoe in series.shoeDetails" :key="shoe.wishId">
              <div class="table-field-1">
                <BaseImage
                  :src="shoe.curtailImg"
                  fit="contain"
                  class="shoe-picture"
                  @click.native="navigatToDetail(shoe.catalogId, shoe.collection, shoe.colorId)" />
              </div>

              <div class="table-field-2 shoe-info">
                <p
                  class="shoe-name"
                  @click="navigatToDetail(shoe.catalogId, shoe.collection, shoe.colorId)">
                  {{ shoe.shoeName }}
                </p>
                <p class="shoe-color">
                  {{$t('order-meeting.intention.color')}}：{{ shoe.colorName }}</p>
                <div class="flex">
                  <p class="shoe-label">
                    {{$t('order-meeting.intention.porduct_number')}}：{{ shoe.barCode }}</p>
                  <p class="shoe-size">
                    {{$t('order-meeting.intention.shoe_size')}}：{{ shoe.shoeSize | array }}</p>
                </div>
              </div>

              <div class="table-field-3 shoe-number">
                <p class="text-one-line">
                  {{ shoe.shoeNumber | number }} {{$t('order-meeting.intention.units')}}
                </p>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </section>

    <!-- 合计 -->
    <section class="total-section">
      <p>
        {{$t('order-meeting.intention.total')}}：
        {{ info.totalNumber | number }} {{$t('order-meeting.intention.units')}}
      </p>
    </section>
  </div>
</template>

<script>
import Vue from 'vue';
import BaseImage from '@/components/base/Image';

import { getWishOrderDetail } from '@/api/order-meeting';
import i18n from 'i18next';

export default Vue.extend({
  name: 'IntentionPreview',
  components: {
    BaseImage,
  },
  filters: {
    number(num) {
      const reg = /\d{1,3}(?=(\d{3})+$)/g;
      return (`${num}`).replace(reg, '$&,');
    },
    array(str) {
      return str.split(/[,，;；\s]/).join(' ');
    },
  },
  data() {
    return {
      info: {
        orderId: '',
        createTime: '',
        salesManagerName: '',
        companyName: '',
        receiverAddress: '',
        phone: '',
        email: '',
        totalNumber: '',
        shoeList: [],
      },
      clientInfoList: [
        { title: i18n.t('order-meeting.intention.company_name'), name: '' },
        { title: i18n.t('order-meeting.intention.shipping_address'), name: '' },
        { title: i18n.t('order-meeting.intention.telephone'), name: '' },
        { title: i18n.t('order-meeting.intention.email'), name: '' },
      ],
    };
  },
  async created() {
    try {
      this.info = (await getWishOrderDetail(this.$route.query.workspaceId)).data;

      const {
        companyName,
        receiverAddress,
        phone,
        email,
      } = this.info;
      this.clientInfoList = [
        { title: i18n.t('order-meeting.intention.company_name'), name: companyName },
        { title: i18n.t('order-meeting.intention.shipping_address'), name: receiverAddress },
        { title: i18n.t('order-meeting.intention.telephone'), name: phone },
        { title: i18n.t('order-meeting.intention.email'), name: email },
      ];
    } catch (err) {
      this.$errHandler(err);
    }
  },
  methods: {
    navigatToDetail(catalogId, collection, colorId) {
      const routeData = this.$router.resolve({
        name: 'OrderMeetingDetail',
        query: {
          workspaceId: this.$route.query.workspaceId,
          collocationId: colorId,
          catalogId,
          collection,
        },
      });

      window.open(routeData.href, '_blank');
    },
  },
});
</script>

